<template>
  <header>
    <div class="header w-100 d-flex justify-content-between px-2 py-2 border bg-light-gray">
      <div class="d-flex">
        <router-link to="/" class="router">
          <div>LOGO</div>
        </router-link>
        <router-link to="/where" class="router">
          <div class="text-nowrap font-weight-bold bg-gray border-15 px-3 py-1 border ml-3">BeBe去哪兒</div>
        </router-link>
        <router-link to="/around" class="router">
          <div class="text-nowrap font-weight-bold bg-gray border-15 px-3 py-1 border ml-3">Be Around You</div>
        </router-link>
        <router-link to="/exchange" class="router">
          <div class="text-nowrap font-weight-bold bg-gray border-15 px-3 py-1 border ml-3">兌換專區</div>
        </router-link>
        <router-link to="/record" class="router">
          <div class="text-nowrap font-weight-bold bg-gray border-15 px-3 py-1 border ml-3">點數紀錄</div>
        </router-link>
      </div>
      <div>人</div>
    </div>
    <div class="header-mobile d-md-none d-flex justify-content-between w-100">
      <router-link to="/"><div @click="navbar = false">logo</div></router-link>
      <div class="nav-btn" @click="navbar = !navbar"></div>
    </div>
    <transition name="slide-fade">
      <div v-if="navbar" class="rightbar d-sm-none">
        <div class="w-fit h-100 rightbar-content h7 text-right p-4">
          <router-link to="/where" >
            <div class="text-nowrap font-weight-bold text-white" @click="navbar = false">BeBe去哪兒</div>
          </router-link>
          <router-link to="/around">
            <div class="text-nowrap font-weight-bold text-white mt-3" @click="navbar = false">Be Around You</div>
          </router-link>
          <router-link to="/exchange">
            <div class="text-nowrap font-weight-bold text-white mt-3" @click="navbar = false">兌換專區</div>
          </router-link>
          <router-link to="/record">
            <div class="text-nowrap font-weight-bold text-white mt-3" @click="navbar = false">點數紀錄</div>
          </router-link>
        </div>
      </div>
    </transition>
  </header>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      navbar: false
    }
  }
}
</script>

<style lang="scss">
.router{
  color: white;
}
.router:hover{
  color:white;
}
.header{
  position: fixed;
  top: 0;
}
</style>



