<template>
  <div class="home mt-5">
    <div class="content p-5 mx-auto">
      <div class="banner"></div>
      <!-- <div class="thenewest text-left mt-5">
        <div class="title-text">最新消息</div>
        <div class="d-flex">
          <div class="w-100 border p-3" v-for="(item, index) in newestList" :key="index" @click="openmodal(item)">
            <p>{{ item.title }}</p>
            <p>{{ item.date }}</p>
          </div>
        </div>
      </div> -->
      <div class="thenewest text-left mt-5">
        <div class="title-text">最新消息</div>
        <div class="py-2 border text-white d-md-none">
          <swiper
            ref="swiperComponent"
            :options="swiperOptions">
            <swiper-slide v-for="(item, index) in newestList" :key="index">
              <div class="w-100 p-3" @click="openmodal(item)">
                <p>the newest</p>
                <p>{{ item.title }}</p>
                <p>{{ item.date }}</p>
                <p>{{ item.date }}</p>
              </div>
            </swiper-slide>
            <div class="swiper-pagination"  slot="pagination"></div>
          </swiper>
        </div>
        <div class="d-md-flex d-none">
          <div class="w-100 border p-3" v-for="(item, index) in newestList" :key="index" @click="openmodal(item)">
            <p>{{ item.title }}</p>
            <p>{{ item.date }}</p>
          </div>
        </div>
      </div>
      <div class="where text-left mt-5">
        <div class="title-text text-left">BeBe去哪兒</div>
        <div class="d-md-flex">
          <div class="w-100 border p-3" v-for="(item, index) in whereList" :key="index" @click="openmodal(item)">
            <p>{{ item.title }}</p>
            <p>{{ item.date }}</p>
          </div>
        </div>
      </div>
      <div class="beround mt-5">
        <div class="title-text text-left mb-2">Be Around You</div>
        <div class="d-md-flex">
          <div class="w-100 border p-3"  v-for="(item, index) in aroundList1" :key="index" @click="openmodal(item)">
            <p>{{ item.title }}</p>
            <p>{{ item.date }}</p>
          </div>
        </div>
        <div class="d-md-flex mt-0 mt-sm-2">
          <div class="w-100 border p-3"  v-for="(item, index) in aroundList2" :key="index" @click="openmodal(item)">
            <p>{{ item.title }}</p>
            <p>{{ item.date }}</p>
          </div>
        </div>
        <p @click="$router.push({name:'Around'})" class="border p-2 w-fit mx-auto mt-3">MORE+</p>
      </div>
      <div class="exchange mt-5">
        <div class="title-text text-left">兌換專區</div>
        <div class="d-md-flex border p-3">
          <div class="w-100 border p-3" v-for="(item, index) in exchangeList" :key="index" @click="openmodal(item)">
            <p>{{ item.title }}</p>
            <p>{{ item.date }}</p>
          </div>
        </div>
        <p @click="$router.push({name:'Exchange'})" class="border p-2 w-fit mx-auto mt-3">MORE+</p>
      </div>
    </div>
  <div v-if="frame" class="frame-modal p-4 d-flex align-items-center">
    <div class="frame bg-white w-100 mx-auto p-5">
      <button @click="frame = false">close</button>
      <div>{{ modaltitle }}</div>
      <div>{{ modaldate }}</div>
      <div>{{ modalcontent }}</div>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      frame: false,
      modalcontent: '',
      modaltitle: '',
      modaldate: '',
      newestList: [
        { title: '1111', date: '11/2', content: 'newestList' },
        { title: '2222', date: '11/2', content: 'newestList' },
        { title: '3333', date: '11/2', content: 'newestList' }
      ],
      whereList: [
        { title: '1111', date: '11/2', content: 'whereList' },
        { title: '2222', date: '11/2', content: 'whereList' },
        { title: '3333', date: '11/2', content: 'whereList' },
        { title: '4444', date: '11/2', content: 'whereList' },
        { title: '5555', date: '11/2', content: 'whereList' }
      ],
      aroundList1: [
        { title: '1111', date: '11/2', content: 'sdrbbbreahwh' },
        { title: '2222', date: '11/2', content: 'sdrbbbreahwh' },
        { title: '3333', date: '11/2', content: 'sdrbbbreahwh' },
        { title: '4444', date: '11/2', content: 'sdrbbbreahwh' }
      ],
      aroundList2: [
        { title: '5555', date: '11/2', content: 'sdrbbbreahwh' },
        { title: '6666', date: '11/2', content: 'sdrbbbreahwh' },
        { title: '7777', date: '11/2', content: 'sdrbbbreahwh' },
        { title: '8888', date: '11/2', content: 'sdrbbbreahwh' }
      ],
      exchangeList: [
        { title: '1111', date: '11/2', content: 'sdrbbbreahwh' },
        { title: '2222', date: '11/2', content: 'sdrbbbreahwh' },
        { title: '3333', date: '11/2', content: 'sdrbbbreahwh' }
      ],
      swiperOptions: {
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        loop: true,
        slidesPerView: 'auto',
        loopedSlides: 3,
        loopAdditionalSlides: 3,
        centeredSlides: true,
        spaceBetween: 15,
        observer: true,
        // observeParents: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }
  },
  methods: {
    openmodal (item) {
      this.frame = true
      this.modalcontent = item.content
      this.modaldate = item.date
      this.modaltitle = item.title
    }
  }
}
</script>

<style lang="scss">
.banner{
  background: gray;
  height: 400px;
  width: 100%;
}
</style>



