import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/where',
      name: 'Where',
      component: () => import('@/views/Where.vue')
    },
    {
      path: '/around',
      name: 'Around',
      component: () => import('@/views/Around.vue')
    },
    {
      path: '/exchange',
      name: 'Exchange',
      component: () => import('@/views/Exchange.vue')
    },
    {
      path: '/record',
      name: 'Record',
      component: () => import('@/views/Record.vue')
    },
    {
      path: '/intro',
      name: 'Intro',
      component: () => import('@/views/Intro.vue')
    },
    {
      path: '/notice',
      name: 'Notice',
      component: () => import('@/views/Notice.vue')
    },
    {
      path: '/qanda',
      name: 'Qanda',
      component: () => import('@/views/Qanda.vue')
    },
    {
      path: '/signup',
      name: 'Signup',
      component: () => import('@/views/Signup.vue')
    }
  ]
})



